import BaseController from 'lib/base-controller';

import MainTemplate from 'templates/main.html';


/**
 * MainController
 */
export default class MainController extends BaseController {

    /**
     * constructor
     */
    constructor (...args) {
        super(...args);

        this.template = MainTemplate;

        this.render();
    }
}



